html, body {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
}

body {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  background-color: #000;
  color: #fff;
}
body.bg-black {
  background-color: #000;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  color: #fff;
}

.player-app {
  margin: auto;
}
.player-app .pixi-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  font-size: 0;
}
.player-app .pixi-wrapper canvas {
  width: 100%;
  height: 100%;
}

.fatal-error {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(92, 6, 6, 0.85);
  background-image: url("../../static/fa/exclamation-triangle-solid.svg");
  background-size: 100px 100px;
  background-repeat: no-repeat;
  background-position: center 50px;
  padding-top: 160px;
}
.fatal-error .fatal-error-text {
  color: white;
  font-size: 20px;
  text-align: center;
  top: 100px;
  left: 50%;
  width: 600px;
  margin: auto;
  margin-bottom: 0.5em;
  padding-left: 15px;
  padding-right: 15px;
}
.fatal-error .fatal-error-details {
  color: white;
  font-size: 16px;
  text-align: center;
  width: 600px;
  margin: auto;
  padding-left: 15px;
  padding-right: 15px;
}

html.with-fatal-error {
  width: 100%;
  height: 100%;
}
html.with-fatal-error body {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.connection-state-view {
  display: none;
  pointer-events: none;
  position: absolute;
  width: 300px;
  padding-top: 135px;
  top: 50%;
  left: 50%;
  margin-top: -150px;
  margin-left: -150px;
  color: black;
  font-size: 20px;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 10px;
}
.connection-state-view.visible {
  display: block;
}
.connection-state-view .connection-state-view-icon {
  display: block;
  width: 100px;
  height: 100px;
  position: absolute;
  top: 25px;
  left: 100px;
}
.connection-state-view .connection-state-view-error {
  font-weight: bold;
}
.connection-state-view .connection-state-view-status {
  font-size: 0.85em;
  margin-bottom: 1.2em;
}